.icon-logo {
	fill: $color_secondary;
	width: 180px;
	height: 74px;
	@include r(1199) {
		width: 140px;
		height: 57px; } }

.scroll-down {
	@include vcenter_gor;
	bottom: 30px;
	cursor: pointer;

	&__arrow,
	&__arrow:before,
	&__arrow:after {
		position: absolute;
		left: 50%; }

	&__arrow {
		width: 30px;
		height: 30px;
		top: 50%;
		margin: -20px 0 0 -20px;
		transform: rotate(45deg);
		border-left: none;
		border-top: none;
		border-right: 2px $color_t solid;
		border-bottom: 2px $color_t solid; }

	&__arrow:before {
		content: "";
		width: 20px;
		height: 20px;
		top: 50%;
		margin: -10px 0 0 -10px;
		border-left: none;
		border-top: none;
		border-right: 1px $color_secondary solid;
		border-bottom: 1px $color_secondary solid;
		animation-duration: 2s;
		animation-iteration-count: infinite;
		animation-name: arrow; }

	&__arrow:after {
		content: "";
		width: 20px;
		height: 20px;
		top: 50%;
		margin: -10px 0 0 -10px;
		border-left: none;
		border-top: none;
		border-right: 2px $color_secondary solid;
		border-bottom: 2px $color_secondary solid;
		opacity: 0;
		transition: all .6s;
		transform: translate(5px, 5px); }

	&:hover &__arrow:after {
		opacity: 1;
		transform: translateY(0); } }

@keyframes arrow {
	0% {
		opacity: 1; }
	100% {
		opacity: 0;
		transform: translate(-10px,-10px); } }

.read-more {
	font-size: 20px;
	font-family: $font_secondary;
	line-height: 1.1;
	text-transform: uppercase;
	color: $color_t;
	text-decoration: none; }
