.about {
  font-size: 20.8px;
  line-height: 1.54;
  &__links {
    font-size: 24px;
    font-family: $font_secondary;
    margin: 40px 0; }
  &__link {
    color: $color_t;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .3s;
    &:hover {
      color: $color_secondary; }
    &:after {
      content: '';
      position: relative;
      width: 3px;
      height: 0.84em;
      background: $color_secondary;
      display: inline-block;
      vertical-align: middle;
      margin: 0 25px; }
    &:last-child:after {
      display: none; } } }
