.nav {
  font-family: $font_secondary;
  font-size: 0;
  &__item {
    display: inline-block;
    text-transform: uppercase;
    &:last-child .nav__link {
      border-right: none; } }
  &__link {
    font-size: 18px;
    text-decoration: none;
    color: $color_t;
    border-right: 2px solid $color_secondary;
    @include r(1199) {
      font-size: 16px; }
    &:hover {
      color: $color_secondary; } }
  @include nav_effect_1;
  @include r(991) {
    display: none; } }
