html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-decor {
  width: 1.54em;
  height: 1em;
  fill: #00AAEF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 2.42em;
  height: 1em;
  fill: initial;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

.nav .nav__link {
  display: inline-block;
  position: relative;
  overflow: hidden;
  padding: 2px 20px;
  line-height: 1;
}

@media only screen and (max-width: 1199px) {
  .nav .nav__link {
    padding: 1px 15px;
  }
}

.nav .nav__link:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  background: #1bb4d0;
  transform: translate3d(102%, 0, 0);
  transition: transform 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.nav .nav__link:hover::after {
  transform: translate3d(0, 0, 0);
}

.nav .nav__link:hover .nav__t:before {
  width: 100%;
}

.nav .nav__t {
  display: block;
  position: relative;
}

.nav .nav__t:before {
  content: attr(data-letters);
  position: absolute;
  color: #ffffff;
  left: 0;
  overflow: hidden;
  white-space: nowrap;
  width: 0%;
  transition: width 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

@font-face {
  font-family: "affect bold";
  src: url("../fonts/affect_bold-webfont.woff2") format("woff2"), url("../fonts/affect_bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

