}

.scroll-down {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: 30px;
  cursor: pointer;
}

.scroll-down__arrow, .scroll-down__arrow:before, .scroll-down__arrow:after {
  position: absolute;
  left: 50%;
}

.scroll-down__arrow {
  width: 30px;
  height: 30px;
  top: 50%;
  margin: -20px 0 0 -20px;
  transform: rotate(45deg);
  border-left: none;
  border-top: none;
  border-right: 2px #29262d solid;
  border-bottom: 2px #29262d solid;
}

.scroll-down__arrow:before {
  content: "";
  width: 20px;
  height: 20px;
  top: 50%;
  margin: -10px 0 0 -10px;
  border-left: none;
  border-top: none;
  border-right: 1px #1bb4d0 solid;
  border-bottom: 1px #1bb4d0 solid;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: arrow;
}

.scroll-down__arrow:after {
  content: "";
  width: 20px;
  height: 20px;
  top: 50%;
  margin: -10px 0 0 -10px;
  border-left: none;
  border-top: none;
  border-right: 2px #1bb4d0 solid;
  border-bottom: 2px #1bb4d0 solid;
  opacity: 0;
  transition: all .6s;
  transform: translate(5px, 5px);
}

.scroll-down:hover .scroll-down__arrow:after {
  opacity: 1;
  transform: translateY(0);
}

@keyframes arrow {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(-10px, -10px);
  }
}

.read-more {
  font-size: 20px;
  font-family: "affect bold", sans-serif;
  line-height: 1.1;
  text-transform: uppercase;
  color: #29262d;
  text-decoration: none;
}

.sc {
  padding: 60px 0;
}

.sc__heading {
  position: relative;
  padding: 60px 0;
  line-height: 1;
}

.sc__heading:before {
  content: '';
  position: relative;
  width: 3px;
  height: 0.84em;
  background: #1bb4d0;
  display: inline-block;
  vertical-align: middle;
  margin-right: 40px;
  margin-left: -44px;
}

.sc__heading--bwhite:before {
  background: #ffffff;
}

.header {
  margin-top: 20px;
}

.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__burger {
  display: none;
}

@media only screen and (max-width: 991px) {
  .header__burger {
    display: block;
  }
}

.header__functional {
  display: flex;
}

.lang__list {
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
  border: 2px solid #1bb4d0;
}

.lang__item {
  font-family: "affect bold", sans-serif;
  color: #29262d;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.burger {
  cursor: pointer;
}

.burger__ico {
  position: relative;
  display: inline-block;
  width: 15px;
  height: 13px;
  top: -1px;
  margin-right: 10px;
  vertical-align: middle;
}

.burger__text {
  font-family: "affect bold", sans-serif;
  transition: all 0.3s ease-in-out;
}

.burger__bar {
  position: absolute;
  height: 3px;
  background-color: #202121;
  overflow: hidden;
}

.burger__bar:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: #1bb4d0;
  position: absolute;
  top: 0;
  left: 100%;
  z-index: 1;
  transition: 0.3s;
}

.burger__bar:nth-child(1) {
  top: 0;
  width: 9px;
}

.burger__bar:nth-child(1):after {
  transition-delay: 0.1s;
}

.burger__bar:nth-child(2) {
  top: 5px;
  width: 15px;
}

.burger__bar:nth-child(2):after {
  transition-delay: 0.2s;
}

.burger__bar:nth-child(3) {
  top: 10px;
  width: 12px;
}

.burger__bar:nth-child(3):after {
  transition-delay: 0.3s;
}

.burger:hover .burger__text {
  color: #1bb4d0;
}

.burger:hover .burger__bar:after {
  left: 0;
}

.search-btn {
  margin-right: 20px;
  background: none;
  border: 0;
  color: inherit;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  appearance: none;
  user-select: none;
}

.nav {
  font-family: "affect bold", sans-serif;
  font-size: 0;
}

.nav__item {
  display: inline-block;
  text-transform: uppercase;
}

.nav__item:last-child .nav__link {
  border-right: none;
}

.nav__link {
  font-size: 18px;
  text-decoration: none;
  color: #29262d;
  border-right: 2px solid #1bb4d0;
}

@media only screen and (max-width: 1199px) {
  .nav__link {
    font-size: 16px;
  }
}

.nav__link:hover {
  color: #1bb4d0;
}

@media only screen and (max-width: 991px) {
  .nav {
    display: none;
  }
}

.welcome {
  position: relative;
  height: calc(100vh - 100px);
  min-height: 500px;
}

@media only screen and (max-width: 1199px) {
  .welcome {
    height: calc(100vh - 82px);
  }
}

.welcome__container {
  height: 90%;
  padding-top: 15px;
}

.welcome__video {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.welcome__placeholder {
  height: 100%;
  background: #b1b1b1;
}

#bgvid {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  background: url("//demosthenes.info/assets/images/polina.jpg") no-repeat;
  background-size: cover;
  transition: 1s opacity;
}

.about {
  font-size: 20.8px;
  line-height: 1.54;
}

.about__links {
  font-size: 24px;
  font-family: "affect bold", sans-serif;
  margin: 40px 0;
}

.about__link {
  color: #29262d;
  text-transform: uppercase;
  text-decoration: none;
  transition: color .3s;
}

.about__link:hover {
