}

.about__link:after {
  content: '';
  position: relative;
  width: 3px;
  height: 0.84em;
  background: #1bb4d0;
  display: inline-block;
  vertical-align: middle;
  margin: 0 25px;
}

.about__link:last-child:after {
  display: none;
}

.founder {
