.header {
  margin-top: 20px;
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__burger {
    display: none;
    @include r(991) {
      display: block; } }
  &__functional {
    display: flex; } }

.lang {
	&__list {
		position: relative;
		width: 40px;
		height: 40px;
		display: inline-block;
		border: 2px solid $color_secondary; }
	&__item {
    font-family: $font_secondary;
    color: $color_t;
    @include vcenter; } }

.burger {
  cursor: pointer;
  &__ico {
    position: relative;
    display: inline-block;
    width: 15px;
    height: 13px;
    top: -1px;
    margin-right: 10px;
    vertical-align: middle; }
  &__text {
    font-family: $font_secondary;
    transition: all 0.3s ease-in-out; }
  &__bar {
    position: absolute;
    height: 3px;
    background-color: #202121;
    overflow: hidden;
    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      background: $color_secondary;
      position: absolute;
      top: 0;
      left: 100%;
      z-index: 1;
      transition: 0.3s; }
    &:nth-child(1) {
      top: 0;
      width: 9px;
      &:after {
        transition-delay: 0.1s; } }
    &:nth-child(2) {
      top: 5px;
      width: 15px;
      &:after {
        transition-delay: 0.2s; } }
    &:nth-child(3) {
      top: 10px;
      width: 12px;
      &:after {
        transition-delay: 0.3s; } } }
  &:hover &__text {
    color: $color_secondary; }
  &:hover &__bar:after {
    left: 0; } }

.search-btn {
  margin-right: 20px;
  @include reset_btn; }
